var write = $.orderInfo;
//
function InitValidator() {

	//
	$.validator.addMethod("demostic", function (value, element) {

		value = value.replace(/^\s+|\s+$/g, '');
		var	demostic = /^([\u4e00-\u9fa5a-zA-Z]+)$/;

		return this.optional(element) || (demostic.test(value));

	}, "");


	//
	$.validator.addMethod("required_m", function(value, element){
		
		var holder = $(element).attr("placeholder");
		
		if( $(element).attr("data-required") === "required" ){
			return value != "" && value != holder;
		}
		
		return true;
		
	}, "");


	//
	$.validator.addMethod("subRequired", function(value, element){
		
		value = value.replace(/^\s+|\s+$/g, "");
		var holder = $(element).attr("placeholder");
		
		if( value != "" && value != holder )	return true;
		
		var inputs = $(element).closest(".guest").find("input");
		
		for( var i = 0; i < inputs.length; i++ ){
			
			var o = inputs[i];
			
			var n_value = o.value.replace( $(o).attr("placeholder"), "" )
								 .replace(/^\s+|\s+$/g, "");
			
			if( n_value != "" )
				return false;
			
		}
		
		return true;
		
	}, "");


	var o = {
		rules: {
			surname: {
				required_m: true,
				subRequired: true,
			},
			userName: {
				required_m: true,
				subRequired: true,
			},
			voucherEmail: {
				email: true
			},
			voucherFax: {
				number: true,
				rangelength: [6, 14]
			},
			voucherMobile: {
				number: true,
				rangelength: [6, 14]
			},
      willUsedBalance: {
			  number: true,
        min: 0
      }
		},
		messages: {
			voucherEmail: {
				email: ''
			},
			voucherFax: {
				number: '',
				rangelength: '6-14'
			},
			voucherMobile: {
				number: '',
				rangelength: '614',
			},
      willUsedBalance: {
        number: '',
        min: '0',
        max: ''
      }
		}
	};

	var o1 = {};
	if (write.content.staticInfo.country == 70007) {	//
		o1 = {
			rules: {
				surname: {
					demostic: true,
				},
				userName: {
					demostic: true
				},
			},
		};
	} else {		//
		o1 = {
			rules: {
				surname: {
					letter: true
				},
				userName: {
					letter: true
				},
				countryId: {
					required_m: true,
					subRequired: true,
				},
			},
		};
	}

	$.extend(true, o, o1);
	$("#orderForm").validate(o);
}

module.exports = InitValidator;



