const getCheck = require('./sendRequest.js').getCheck;

function isOnline() {
  //
  getCheck(function (checkData) {
    if (checkData.isOnline) {
      //
      const initPage = require('./initPage.js');
      initPage.getData();
  
    } else {
      //
      layer.alert('');
      //
      CloseWebPage();
    }
  });
}

function CloseWebPage() {
  if (navigator.userAgent.indexOf("MSIE") > 0) {
    if (navigator.userAgent.indexOf("MSIE 6.0") > 0) {
      window.opener = null;
      window.close();
    }
    else {
      window.open('', '_top');
      window.top.close();
    }
  }
  else if (navigator.userAgent.indexOf("Firefox") > 0) {
    window.location.href = 'about:blank '; //window.openwindow.close
    //window.history.go(-2);
  }
  else {
    window.opener = null;
    window.open('', '_self', '');
    window.close();
  }
}

module.exports = {
  run : function () {
    isOnline();
  }
};



